import WordDisplay from "../WordDisplay/WordDisplay";
import Keyboard from "../Keyboard/Keyboard";
import { RootState, useSelector } from "../../store/store";
import { useNavigate } from "react-router-dom";

interface GameInfoProps {
  currentRound: number;
  totalRounds: number;
  word: string;
  hint: string;
  incorrectGuesses: number;
  maxAttempts: number;
  timeLeft: number;
  isGameWon: boolean;
  gameOver: boolean;
  restart: () => void;
}

const GameInfo = ({
  currentRound,
  totalRounds,
  word,
  hint,
  incorrectGuesses,
  maxAttempts,
  timeLeft,
  isGameWon,
  gameOver,
  restart,
}: GameInfoProps) => {
  const guessedLetters = useSelector((state: RootState) => state.game.guessedLetters);
  const navigate = useNavigate();

  return (
    <div className="game-info">
      <h2>: {currentRound} / {totalRounds}</h2>
      <p>: {hint}</p>
      <WordDisplay word={word} guessedLetters={guessedLetters} />
      <p>: {incorrectGuesses} / {maxAttempts}</p>
      <p>: {timeLeft} </p>
      {!gameOver && !isGameWon && <Keyboard />}
      {isGameWon && <p className="result success"> !</p>}
      {gameOver && !isGameWon && <p className="result failure"> </p>}
      {gameOver && <button className="restart-btn" onClick={restart}> </button>}
      <button
        className="restart-btn"
        style={{ marginLeft: 8 }}
        onClick={() => navigate("/")}
      >
           
      </button>
    </div>
  );
};

export default GameInfo;
